@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&family=Roboto+Mono&display=swap);
:root {
  --primary: #232F3E;
  --secondary: rgb(223,92,163);
  --tertiary: rgba(255, 157, 0, 0.984);
  --light: #fafafa;
  --dark: #272f32;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

.video{
  width: 100%;
}

html,
body {
  min-height: 100%;
  background: linear-gradient(to right, rgba(251, 194, 235, 1), rgba(166, 193, 238, 1)) !important;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: var(--dark);
}


h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Alfa Slab One', cursive;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
  font-family: 'Roboto Mono', monospace;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
  font-family: 'Roboto Mono', monospace;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--dark);
}

header a {
  color: var(--dark);
}

.header-image {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 10%;
  background-size:cover;
  background-image: url(/static/media/checker.ae50b84c.png);
}

.site-logo {
  position: absolute;
  display: flex;
  top: 2px;
  left: 40px;
  height: 90px;
  width: 90px;
  cursor: pointer;
  transition: transform 0.2s ease;
  background-size: cover;
  background-image: url(/static/media/turniplogo.2658e1d3.png);
}

.site-logo:hover {
  opacity: .9;
  transform: scale(1.1);
}

.nav__links {
  display: inline-block;
  padding: 0px 20px;
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem !important; 
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
  font-family: 'Roboto Mono', monospace;
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

/* signup/login pages */
.signup-container, .login-container{
  display: flex;
  flex-direction: column;
  width: 85%;
  margin: auto;
  font-family: 'Roboto Mono', monospace;
  margin-top: 90px;
  justify-content: center;
  align-items: center;
}

.success, .noMatch{
  background-image: url(/static/media/formbg.9056d79b.png);
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 85%;
  padding: 90px;
  margin: auto;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.signup-page, .login-page{
  background-image: url(/static/media/formbg.9056d79b.png);
  background-size: cover;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.history{
  text-align: center;
  margin: 40px;
}

.form-link{
  margin: 30px;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: var(--error);
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}
/* For devices with screen widths less than or equal to 480px */
@media only screen and (max-width: 480px) {
  .nav__links{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  .site-logo{
    left: 0px;
    
  }
}




.cart {
  position: fixed;
  top: 0;
  right: 0;
  min-width: 20%;
  max-width: 30%;
  max-height: 60%;
  background-color: var(--light);
  overflow: auto;
  padding: .5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  border-bottom-left-radius: .5rem;
}

.cart h2 {
  font-size: 1.5rem;
  border-bottom: 1px solid var(--dark);
  padding-bottom: .5rem;
  margin: 1rem 0;
}

.cart .close {
  position: absolute;
  top: .5rem;
  right: .5rem;
  cursor: pointer;
  color: var(--tertiary);
}

.cart .close:hover {
  text-decoration: underline;
}

.cart-closed {
  position: fixed;
  top: 2%;
  right: 1%;
  font-size: 2rem;
  cursor: pointer;
  background-color: var(--secondary);
  border-radius: 50%;
  padding: .25rem;
  width: 50px;
  height: 50px;
}

.cart-closed:hover {
  transform: rotate(8deg);
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 .5rem 1rem 0;
}

@media screen and (max-width: 768px) {
  .cart {
    position: relative;
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    position: relative;
    margin: 0 auto;
  }
}

.cardGroup {
  padding-top: 20px;
  padding-bottom: 20px;
  background: linear-gradient(
    to right,
    rgba(251, 194, 235, 1),
    rgba(166, 193, 238, 1)
  ) !important;
}
.cardBody {
  background: black;
  color: white !important;
}
.cardImg {
  transition: transform 0.2s ease;
}

.cardImg:hover {
  transform: scale(1.2);
}

.card-title {
  padding: 10%;
  font-size: 2rem !important;
}

@media (max-width: 575px) {
  .cardGroup {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .card {
    margin: 10px auto;
  }
}

.posts {
  margin: 20px;
  background: linear-gradient(
    to right,
    rgba(251, 194, 235, 1),
    rgba(166, 193, 238, 1)
  ) !important;
  padding: 5%;
}
.postContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.newPost {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  margin-top: 20px;
}

input[type="text"] {
  width: 50%;
  height: 50px;
  margin-right: 10px;
  font-size: 1.2em;
}
.postSubmitBtn {
  width: 20%;
  height: 50px;
  font-size: 1.2em;
}

.commentText {
  font-size: 13px;
  line-height: 18px;
  color: black;
  display: block;
  /* background: #f6f6f7; */
  border: 1px solid #000000;
  border-radius: 0.5rem;
  padding: 15px 20px 20px 20px;
  width: auto;
}

.form-input {
  font-size: 13px;
  line-height: 50px;
  font-weight: 400;
  color: #b4b7c1;
  width: 100%;
  height: 50px;
  padding-left: 20px;
  padding-right: 20px;
  border: 1px solid #edeff2;
  border-radius: 3px;
}

.form-input {
  align-items: center;
  justify-content: center;
  height: 150px;
  width: 80%;
}

.btn {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 5px;
  /* margin-bottom: 40px; */
  background-color: var(--secondary);
  color: black;
  font-family: "Roboto Mono", monospace;
}

.postDiv {
  padding: 10px;
  margin-top: 30px;
  width: 80%;
}

